@import '../../assets/styles/app';

.getInstant {
  padding-top: 80px;
  padding-bottom: 62px;

  @media (min-width: 920px) {
    padding-top: 168px;
    padding-bottom: 100px;
  }

  .inner {
    @include inner;
    position: relative;
    display: flex;
    flex-direction: column;

    .titleWrapper {
      // @include h2_block;
      // @include h2_block_white;

      .pretext {
        font-weight: 500;
        font-size: 12px;
        line-height: 115%;
        text-align: center;
        letter-spacing: -0.02em;
        text-transform: uppercase;
        color: rgba(255, 255, 255, 0.4);

        @media (min-width: 800px) {
          font-size: 14px;
        }
      }

      .title {
        margin-top: 10px;
        font-size: 24px;
        font-weight: 600;
        line-height: 130%;
        text-align: center;
        letter-spacing: -0.02em;
        color: #fff;

        width: 100%;
        margin-left: auto;
        margin-right: auto;

        @media (min-width: 800px) {
          width: 500px;
          margin-left: auto;
          margin-right: auto;
          margin-top: 15px;
          font-size: 36px;
        }
      }
    }

    .rocket {
      display: block;
      position: absolute;
      z-index: 1;
      left: 50%;
      transform: translateX(-50%);
      width: auto;
      height: 387px;
      top: 0;

      @media (min-width: 920px) {
        display: none;
      }
    }

    .videoWrapper {
      pointer-events: none;
      display: none;
      position: absolute;
      z-index: 1;
      left: 50%;
      transform: translateX(-50%);
      top: 0;
      width: 467px;
      height: 387px;

      @media (min-width: 920px) {
        display: block;
        width: 887px;
        height: 737px;
        top: 26px;
        left: calc(50% - 3px);
      }

      video {
        object-fit: cover;
        width: 100%;
        height: 100%;
      }
    }

    .items {
      position: relative;
      z-index: 2;
      margin-top: 323px;

      @media (min-width: 920px) {
        margin-top: 85px;
        display: flex;
        flex-direction: column;
        order: 2;
      }

      .item {
        position: relative;
        box-sizing: border-box;
        border: 1px solid rgba(255, 255, 255, 0.06);
        background: rgba(21, 24, 40, 0.8);
        backdrop-filter: blur(17px);
        border-radius: 15px;
        padding: 30px 20px 20px;

        @media (min-width: 920px) {
          padding: 25px 25px 25px 95px;
          width: 413px;
        }

        &:nth-child(2) {
          margin-top: 20px;

          @media (min-width: 920px) {
            margin-top: -73px;
            align-self: flex-end;
          }
        }

        &:nth-child(3) {
          margin-top: 20px;

          @media (min-width: 920px) {
            width: 423px;
            margin-top: 70px;
            margin-left: 217px;
          }
        }

        .title {
          font-weight: 600;
          font-size: 18px;
          line-height: 130%;
          letter-spacing: -0.02em;
          color: #ffffff;
          margin-left: 55px;

          @media (min-width: 920px) {
            font-size: 24px;
            margin-left: 0;
          }
        }

        .text {
          margin-top: 15px;
          font-weight: 400;
          font-size: 13px;
          line-height: 150%;
          letter-spacing: -0.01em;
          color: #ffffff;
          opacity: 0.6;

          @media (min-width: 920px) {
            font-size: 14px;
            color: #a1a3a9;
            opacity: 1;
          }
        }

        .iconWrapper {
          @include centerRow;
          position: absolute;
          top: 20px;
          left: 20px;
          width: 40px;
          height: 40px;
          background: #1c1f31;
          border-radius: 10px;

          @media (min-width: 920px) {
            width: 50px;
            height: 50px;
            top: 25px;
            left: 25px;
          }

          svg {
            width: 20px;
            height: 20px;

            @media (min-width: 920px) {
              width: 26px;
              height: 26px;
            }
          }
        }
      }
    }

    .buttons {
      position: relative;
      z-index: 2;
      margin-top: 30px;
      display: flex;
      align-items: center;
      justify-content: center;

      @media (min-width: 920px) {
        margin-top: 20px;
      }

      .joinBtn {
        width: 130px;

        @media (min-width: 920px) {
          width: 137px;
        }
      }

      .learnBtn {
        margin-left: 20px;

        @media (min-width: 920px) {
          margin-left: 35px;
        }
      }
    }
  }
}
